<template lang="html">
  <!-- 条件筛选 -->
  <div class="search">
    <el-form :model="formData" size="small" label-width="80px" inline>
      <FormInput
        @keyup.native.enter="$emit('on-search', formData)"
        @on-change="$set(formData, 'name', $event)"
        label="接口名称"
        width="174px"
        ref="name"
      />

      <FormSelect
        @on-change="$set(formData, 'interfaceTypeId', $event)"
        httpAPIName="fetchTransactionPortType"
        :httpAPIParams="{ params: { type: 1 } }"
        labelAttr="name"
        valueAttr="dictionaryId"
        prop="interfaceTypeId"
        label="接口类型"
        ref="interfaceTypeId"
      />

      <FormSelect
        @on-change="$set(formData, 'payTypeId', $event)"
        httpAPIName="fetchTransactionPaymentType"
        :httpAPIParams="{ params: { pageNo: 1, pageSize: 100 } }"
        labelAttr="name"
        valueAttr="id"
        prop="payTypeId"
        label="支付类型"
        ref="payTypeId"
      />

      <FormSelectStatic
        @on-change="$set(formData, 'status', $event)"
        :options="[
          { value: 0, label: '启用' },
          { value: 1, label: '禁用' },
        ]"
        label="启用状态"
        width="100px"
        ref="status"
      />

      <div style="display: inline-block">
        <SearchIcon @click.native="search" />
        <SearchReset @click.native="reset" />
      </div>
    </el-form>
  </div>
</template>

<script type="text/javascript">
import FormInput from '@/components/form/FormInput'
import FormSelect from '@/components/form/FormSelect'
import FormSelectStatic from '@/components/form/FormSelectStatic'
import SearchIcon from '@/components/search/SearchIcon'
import SearchReset from '@/components/search/SearchReset'

export default {
  name: 'PaymentPortSearch',
  components: {
    FormInput,
    FormSelect,
    FormSelectStatic,
    SearchIcon,
    SearchReset
  },
  data () {
    return {
      formData: {}
    }
  },
  methods: {
    search () {
      this.$emit('on-search', this.formData)
    },
    reset () {
      for (let key in this.$refs) {
        this.$refs[key].reset()
      }

      this.formData = {}
    }
  }
}
</script>
